import React from "react"
import Link from "gatsby-link"
import * as PropTypes from "prop-types"
import ReactMarkdown from "react-markdown"

import { rhythm } from "../utils/typography"

const propTypes = {
  data: PropTypes.object.isRequired,
}

class RecipeTemplate extends React.Component {
  render() {
    const recipe = this.props.data.contentfulRecipe
    
    return (
      <div>
        <h1>{recipe.title}</h1>
        <section>
          <h3>Ingredients</h3>
          <ReactMarkdown source={recipe.ingredients.ingredients} />
        </section>
        <section>
          <h3>Instructions for prep</h3>
          <ReactMarkdown source={recipe.instructions.instructions} />
        </section>
      </div>
    )
  }
}

RecipeTemplate.propTypes = propTypes

export default RecipeTemplate

export const pageQuery = graphql`
  query recipeQuery($id: String!) {
    contentfulRecipe(id: { eq: $id }) {
      title
      ingredients {
        ingredients
      }
      instructions {
        instructions
      }
    }
  }
`



